import React from 'react';
import { Redirect } from 'react-router-dom';
import { Route } from 'react-router-dom';
// lazy load all the views

// form
const latan = React.lazy(() => import('../pages/Aerolineas/Latan.js'));
const avianca = React.lazy(() => import('../pages/Aerolineas/avianca.js'));
const copa = React.lazy(() => import('../pages/Aerolineas/copa.js'));
const forms = React.lazy(() => import('../pages/forms/Form.js'));
// - other
// root routes
const Aerolineas = React.lazy(() => import('../pages/Aerolineas'));
const rootRoute = {
    path: '/',
    exact: true,
    component: () => <Redirect to="/pages/Aerolineas" />,
    route: Route,
};

// dashboards
const dashboardRoutes = {
    path: '/pages/',
    name: 'Pages',
    icon: 'uil-home-alt',
    header: 'Navigation',
    children: [
        {
            path: '/pages/Aerolineas',
            name: 'Aulaweb',
            component: Aerolineas,
            route: Route,
        },
        {
            path: '/pages/aerolineas/latan',
            name: 'Latan',
            component: latan,
            route: Route,
        },
        {
            path: '/pages/aerolineas/avianca',
            name: 'Avianca',
            component: avianca,
            route: Route,
        },
        {
            path: '/pages/aerolineas/copa',
            name: 'Copa',
            component: copa,
            route: Route,
        },
        {
            path: '/pages/forms/form',
            name: 'Forms',
            component: forms,
            route: Route,
        },
    ],
};

// flatten the list of all nested routes
const flattenRoutes = (routes) => {
    let flatRoutes = [];

    routes = routes || [];
    routes.forEach((item) => {
        flatRoutes.push(item);

        if (typeof item.children !== 'undefined') {
            flatRoutes = [...flatRoutes, ...flattenRoutes(item.children)];
        }
    });
    return flatRoutes;
};

// All routes

const authProtectedRoutes = [rootRoute, dashboardRoutes];
const authProtectedFlattenRoutes = flattenRoutes([...authProtectedRoutes]);

export { authProtectedRoutes, authProtectedFlattenRoutes };
